<template lang='jade'>
    h1#comp-title {{ compTitle }}
    h2#hinting-title {{ hintingTitle }}
    <pre-code :code-string="codeString"></pre-code>
    h2#hinting-title {{ hintingTitle2 }}
</template>

<script type="text/javascript">
import formDialog from 'formDialog'
import preCode from './preCode.vue'

export default {
    data () {
        return {
            compTitle : "Component Name",
            hintingTitle: ":",
            hintingTitle2: ":",
            codeString: `
    import formDialog from 'formDialog';; //

    //;
    <form-dialog></form-dialog>

    // vue
    var dlgMsgObj = {
	    //*Tools
	    Tools: Tools, 
	    //*
	    userName: '',       
	    userPhone: '<b>*</b>',
	    userAddress: '',
	    leftBtnText: '',
	    rightBtnText: '',
	    namePlaceholder: '',
	    phonePlaceholder: '',
	    addressPlaceholder: '',
	    verifyPhoneNumReg: null,    //;
	    callBackFunc: null,         //(succ tips)
	}
	this.$broadcast('show-form-dlg', dlgMsgObj);
    `
        }
    },
    route:{
		data(transition){
            this.compTitle = transition.to.name
		}
	},
    components: {
        formDialog,
        preCode,
    },
    methods: {
    },
    events: {
        'on-countdown-finish': function (countNum) {
            // countNum  countDown ;
            alert("countdown had down; num = " + countNum )
        }
    }
}
</script>


